import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function tangibleBookValuePerShareTtm(tableName: string) {
  const title = "Tangible BV/Share (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["tangibleBookValuePerShareTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Physical net assets per share.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about valuations measures like tangible book value per share?`,
              value:
                info.row.original.financialData.companyRatios?.data?.tangibleBookValuePerShareTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.tangibleBookValuePerShareTtm}
        />
      )
    },
  })
}

export { tangibleBookValuePerShareTtm }
